<template>
    <Menu ref="leftMenu" theme="dark" mode="vertical" width="auto" class="left-menu" @on-select="leftMenu" :open-names="navNames" accordion>
        <MenuItem  name="/home"><Icon type="md-home" size="18"></Icon>&nbsp;首页</MenuItem>
        
       
    </Menu>
</template>

<script>
	import axios from 'axios'
	import { mapState, mapMutations } from 'vuex'
    import { baseUrl, getNewTagList } from '@lib/config'
    
    export default {
        name: 'LeftMenu',
    	data(){
    		return{
    			menuList: null,
    			navNames: [],
    			acvtie: null
    		}
    	},
    	computed: mapState({ 
            user: state => state.User,
            prem: state => state.User.userPermission,
        }),
    	created(){
    		this.getMenu();
    	},
    	watch: {
    		// 监控路由
			'$route' (to, from) {
                let nav = {
                    path: to.path,
                    name: to.name,
                    meta: to.meta
                }
                
                this.setTagNavList(getNewTagList(this.$store.state.Common.tagNavs, nav))
                this.acvtie = '/' + to.name.trim();
                let subAcvtie = to.meta.subTitle.trim();
                this.navNames = [subAcvtie];
				this.$nextTick(() => {
					this.$refs.leftMenu.updateOpened();
					this.$refs.leftMenu.currentActiveName = this.acvtie;
				});
			}
    	},
    	mounted(){
    		this.acvtie = '/' + this.$route.name.trim();
			let subAcvtie = this.$route.meta.subTitle.trim();
            this.navNames = [subAcvtie];
        },
    	methods:{
            ...mapMutations([
                'setTagNavList'
            ]),
    		// 初始化leftMenu 菜单组件数据
    		getMenu(){
                this.menuList = this.user.Menus;
                // 根据路由地址制定leftMenu选中项
                this.$nextTick(() => {
                    this.$refs.leftMenu.updateOpened();
                    this.$refs.leftMenu.currentActiveName = this.acvtie;
                });
    		},
    		leftMenu(menu) {
                this.$Notice.destroy()
    			// // leftMenu 跳转
    			this.$router.replace({ path: menu });
    		}
    	}
    }
</script>

<style lang="less" rel="stylesheet/less">
.left-menu{ height: 100%; overflow: auto; background: url("~@static/images/logo-o-1.png") center no-repeat #515a6e; background-size: 70%; background-position-y: 97%;}
.ivu-icon{
    &-icon-set:before{content: '\F18D'}
    &-icon-finance:before{content: '\F3A5'}
    &-icon-partner:before{content: '\F3F5'}
    &-icon-portrait:before{content: '\F422'}
    &-icon-log:before{content: '\F435'}
    &-icon-box:before{content: '\F394'}
    &-icon-shop:before{content: '\F166'}
    &-icon-brand:before{content: '\F1CF'}
    &-icon-proxy:before{content: '\F421'}
}
</style>


