<template>
	<div class="tags-nav">
		<div class="tags-nav-left">
			<Button type="text" @click="updataOffset(0)">
	        	<Icon :size="20" type="ios-arrow-back" color="#999" />
			</Button>
		</div>
		<div class="tags-nav-right">
			<Button type="text" @click="setScrollToRight">
	        	<Icon :size="20" type="ios-arrow-forward" color="#999" />
			</Button>
		</div>
		<div class="tags-nav-scroll" ref="scrollBox" @DOMMouseScroll="mouseScroll" @mousewheel="mouseScroll">
			<div class="tags-nav-scroll-body" ref="scrollBody" :style="meterStyle">
				<transition-group name="list-complete" tag="p">
					<Tag type="dot" v-for="item in tagList" :name="item.name" :key="`tag-nav-${item.name}`" :color="item.name === tagActive.name ? 'primary' : 'default'" :closable="item.name !== 'home'" @on-close="handleClose" @click.native="handleClick(item)" class="list-complete-item">{{item.meta.title}}</Tag>
				</transition-group>
			</div>
		</div>
	</div>
</template>
<script>
export default{
	name: 'TagsNav',
	props: {
		tagActive: Object,
		tagList: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data(){
		return{
			meterStyle: {
				transform: 'translateX(0px)'
			},
			g_offset: 0,
			offset: 0
		}
	},
	watch: {
		'tagList'(newTagList, oldTagList) {
			this.$nextTick(function() {
				this.setScrollToRight()
			})
		}
	},
	mounted () {
		this.$nextTick(function() {
			this.setScrollToRight()
		})
	},
	methods: {
		handleClose (e, name){
			let box_w = this.$refs.scrollBox.offsetWidth
			let body_w = this.$refs.scrollBody.offsetWidth
			if((body_w - box_w) <= 0){
				this.g_offset = this.offset = 0
				this.updataOffset(0)
			}
			let newNavs = this.tagList.filter(item => item.name !== name)
			this.$emit('close', newNavs, name)
		},
		handleClick (item) {
	    	this.$emit('input', item)
	    },
	    mouseScroll(e){
	    	let delta = 0
			if (e.type === 'DOMMouseScroll' || e.type === 'mousewheel') {
				delta = (e.wheelDelta) ? e.wheelDelta : -(e.detail || 0) * 40
			}

			let box_w = this.$refs.scrollBox.offsetWidth
			let body_w = this.$refs.scrollBody.offsetWidth
			if((body_w - box_w) > 0){
				this.g_offset = (body_w - box_w)
			}else{
				this.g_offset = 0
			}

			if(delta > 0){
				this.offset -= delta
				if(delta <= this.offset){
					this.updataOffset(-(this.offset))
				}else{
					this.offset = 0
					this.updataOffset(this.offset)
				}
			}else{
				this.offset += Math.abs(delta)
				if(this.offset >= this.g_offset){
					this.offset = this.g_offset
					this.updataOffset(-this.g_offset)
				}else{
					this.updataOffset(-this.offset)
				}				
			}
	    },
	    setScrollToRight(){
	    	let box_w = this.$refs.scrollBox.offsetWidth
			let body_w = this.$refs.scrollBody.offsetWidth

			if((body_w - box_w) > 0){
				this.g_offset = this.offset = body_w - box_w
				this.updataOffset(-this.offset)
			}
	    },
	    updataOffset(delta){
	    	this.meterStyle = {
				transform: 'translateX('+ delta +'px)'
			}
	    }
	}
}
</script>
<style lang="less" rel="stylesheet/less">
.tags-nav{
	position: relative; padding: 0 32px;

	&-left{position: absolute; left: 0; top: 0;}
	&-right{position: absolute; right: 0; top: 0;}
	&-left,&-right{
		width: 22px;
		.ivu-btn{padding: 10px 0;}
	}

	&-scroll{
		overflow: hidden;

		&-body{ white-space: nowrap; display: inline-block; transition: all 0.6s; }
	}
}
.list-complete-item {
	transition: all 0.6s;
}
.list-complete-enter, .list-complete-leave-to {
	opacity: 0;
	transform: translateY(-30px);
	z-index: 0;
}
.list-complete-leave-active {
	position: absolute;
}
</style>


