<template>
    <div class="-top-user">
        <Menu mode="horizontal" active-name="userAction" @on-select="headernav" class="-top-user_menu">
            <Submenu name="userAction">
                <template slot="title">
                    <Icon type="md-person" size="20" />{{user}}
                </template>
                <!-- <MenuItem name="updatapwa">
                    <Icon type="md-cog" />修改密码
                </MenuItem> -->
                <MenuItem name="logout">
                    <Icon type="md-log-out" />退出登录
                </MenuItem>
            </Submenu>
        </Menu>
        
        
        <Modal v-model="modifyPassword" title="修改密码" width="40%" @on-cancel="cancel('pwdForm')">
            <Form ref="pwdForm" :model="FormValidate_pwd" :rules="RuleValidate_pwd" :label-width="90">
                <Row>
                    <Col span="24">
                        <FormItem label="新密码" prop="password">
                            <Input type="password" v-model="FormValidate_pwd.password" placeholder="请输入新密码"></Input>
                        </FormItem>
                        <FormItem label="确认新密码" prop="confirmPWD">
                            <Input type="password" v-model="FormValidate_pwd.confirmPWD" placeholder="请输入确认新密码"></Input>
                        </FormItem>
                    </Col>
                </Row>
            </Form>
            <div slot="footer">
                <Button type="text" size="large" @click="cancel('pwdForm')">取消</Button>
                <Button type="primary" size="large" @click="confirmModify('pwdForm')">确定</Button>
            </div>
        </Modal>
    </div>
</template>
<script>
import { mapActions } from 'vuex';
import axios from 'axios';

import { USER_SIGNOUT } from '@store/user';
import { baseUrl } from '@lib/config';

export default {
    name: 'UserMenu',
    props: {
        user: String
    },
    data(){
        const validatePassCheck = (rule, value, callback) => {
            if (value !== this.FormValidate_pwd.password) {
                callback(new Error('两次输入密码不一致'));
            } else {
                callback();
            }
        };

        return{
            modifyPassword:false,
            // 绑定字段
            FormValidate_pwd:{
                username: this.user,
                password: '',
                confirmPWD: ''
            },
            // 修改密码 表单校验规则
            RuleValidate_pwd:{
                
                password: [
                    { required: true, message: '新密码不能为空', trigger: 'blur' },
                    { type: 'string', min: 6, message: '密码不能少于6位', trigger: 'blur' }
                ],
                confirmPWD: [
                    { required: true, message: '确认密码不能为空', trigger: 'blur' },
                    { validator: validatePassCheck, trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        ...mapActions([USER_SIGNOUT]),
        headernav(item) {
            switch (item){
                case 'logout':
                    this.logout();
                    break;
                case 'updatapwa':
                    this.modifyPwd();
                    break;
            }
        },
        logout(){
            this.$Modal.confirm({
                title: '提示',
                content: '<p>确定要退出了吗？</p>',
                onOk: () => {
                    this.USER_SIGNOUT().then(() => {
                        this.$router.replace({ path: '/login' });
                    });
                }
            });
        },
        modifyPwd(){
            this.modifyPassword = true;
        },
        // 修改密码
        confirmModify(form){
            this.$refs[form].validate((valid) => {
                if(valid){
                    axios.post(baseUrl+'/ser/reset',this.FormValidate_pwd).then(res => {
                        if(res.data.respCode == "000000"){
                            // this.$Message.success(res.data.respDesc);
                            this.modifyPassword = false;
                            // 清空表单
                            this.$refs[form].resetFields();
                            this.USER_SIGNOUT().then(() => {
                                this.$router.replace({ path: '/login' });
                            });
                        }else{
                            this.$Message.error(res.data.respDesc);
                        }
                    }).catch(error => {
                        this.$Message.error(error)
                    });
                }
            })
        },
        //关闭 修改密码弹框
        cancel(form){
            this.$refs[form].resetFields();
            this.modifyPassword = false;
        }
    }
}
</script>
<style lang="less" rel="stylesheet/less">
.-top-user{
    cursor: pointer;

    &-pop{
        &_msg{
            text-align: left;
            .-title{
                padding: 8px 16px;
                border-bottom: 1px solid #e8eaec;
            }
            .-msg-null{
                text-align: center; padding: 30px 0;
            }
        }
    }
    &_tips{text-align: center;}
    &_menu{
        height: 49px; line-height: 49px;

        .ivu-menu-submenu-title{
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
        .ivu-menu-submenu{
            padding: 0; float: none; text-align: center;
        }
    }
}
.-top-user_menu.ivu-menu-horizontal.ivu-menu-light:after{
    background: #fff;
}
</style>


