<template>
    <Layout class="layout">
        <Sider hide-trigger width="190" class="layout-left-menu">
            <v-left-menu></v-left-menu>
        </Sider>
        <Layout>
            <Header class="layout-header-bar">
                <v-top-tag-nav :tagList="tagNavList" :tagActive="$route" @input="tagNavClick" @close="tagNavClose" />
                <div class="layout-header-right_user">
                    <v-top-user-menu :user="user.userName" />
                </div>
            </Header>
            <Content class="layout-content-bar">
                <transition name="slide" mode="out-in">
                    <keep-alive :include="cacheList">
                        <router-view></router-view>
                    </keep-alive>
                </transition>
            </Content>
        </Layout>
        
    </Layout>
</template>

<script>
	import leftMenu from './leftMenu';
    import UserMenu from './rightUser';
    import TagNav from './tagNav';

    import { mapState, mapMutations } from 'vuex';
    
    import { baseUrl } from '../../../lib/config';
    import axios from 'axios';

    export default {
    	data(){
    		return{
    			
    		}
    	},
        computed: {
            ...mapState({ user: state => state.User.userInfo }),
            cacheList(){
                return this.tagNavList.length ? this.tagNavList.filter(item => !(item.meta && item.meta.notCache)).map(item => item.name) : []
            },
            tagNavList () {
                return this.$store.state.Common.tagNavs
            }
        },
        methods: {
            ...mapMutations([
                'setTagNavList',
                'addTag'
            ]),
            tagNavClick(nav){
                this.$router.push({
                    name: nav.name
                })
            },
            tagNavClose(navList, name){
                if(this.$route.name === name){
                    this.$router.push({
                        name: navList[navList.length-1].name
                    })
                }
                this.setTagNavList(navList)
            }
        },
        mounted () {
            const nav = {
                path: '/home',
                name: 'home',
                meta: {
                    title: '首页',
                    subTitle: '首页',
                    requireAuth: true,
                }
            }
            this.setTagNavList()
            this.addTag(nav)
        },
		components: {
			'v-left-menu': leftMenu,
            'v-top-user-menu': UserMenu,
            'v-top-tag-nav': TagNav
		}
    }
</script>

<style lang="less" rel="stylesheet/less">
.layout{
    height: 100%;

    &-left-menu{
        position: relative;
        z-index: 2;
    }

    &-header-bar{
        background: #fff; padding: 0 155px 0 10px; position: fixed; left: 190px; 
        right: 0; z-index: 90; height: 49px; line-height: 49px;
        box-shadow: 0 0 5px rgba(0,0,0,0.1); z-index: 2;

    }
    &-header-right_user{
        position: absolute; right: 0px; top: 0; width: 155px;

        .header-nav-list{
            height: 49px; line-height: 49px;
            &:after{background: #fff;}

            .ivu-menu-submenu{padding: 0 6px 0 10px;}
        }
    }
    &-content-bar{
        position: relative; z-index: 1; background: #F2F5F9;
        padding: 65px 15px 15px 15px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
        flex-direction: column;
        -ms-flex: auto;
        flex: auto;
    }
}
</style>


